body {
  margin: 0;
  background: #9999CC;
  /* /* background: rgb(12,8,94); */
  /* background: linear-gradient(180deg, rgb(6, 7, 55) 0%, rgb(43, 13, 83) 98%) no-repeat center fixed; */
  background-size: cover;
  font-family: 'Josefin Sans', sans-serif;
  color: black;
  width: 100%;
  height: 100%;
}

#root {
  margin: 0px;
  width: 100%;
  height: 100%;
}

.app-whole{
  margin: 0px;
  width: 100%;
  height: 100%;
  display: flexbox;
  justify-content: space-evenly;
}

.nav-bar {
  /* display: flexbox; */
  justify-content: space-evenly;
  width: 100%
}

.home-whole {
  height: 100%;
  width: 100%;
  /* border: solid 12px black; */
  text-align: center;
  margin-top: 5%;
}


h1 {
  font-size: 10em;
  margin: 0;
  /* font-weight: 100; */
  /* text-shadow: 8px 8px 3px rgb(255, 255, 255); */
}

h2 {
  font-size: 9em;
  margin: 0;
}

h3 {
  font-size: 8em;
  margin: 0;
  font-style: italic;
  /* text-shadow: 8px 8px 3px white; */
}

h4 {
  font-size: 7.5em;
  margin: 0;
}

h5 {
  font-size: 7em;
  margin: 0;
}
h6 {
  font-size: 6em;
  margin: 0;
}

i:hover {
  text-shadow: 1.5px 1.5px 1.5px rgb(255, 255, 255);
}

